import Joi from '@hapi/joi';

import { Workflow } from '../../constants';
import { BaseAndroidJob, BaseAndroidJobSchema } from '../baseJob';

enum ManagedBuildType {
  APK = 'apk',
  AppBundle = 'app-bundle',
  DevelopmentClient = 'development-client',
}

interface ManagedAndroidJob extends BaseAndroidJob {
  type: Workflow.Managed;
  buildType: ManagedBuildType;
  username?: string;
}

const ManagedAndroidJobSchema = BaseAndroidJobSchema.concat(
  Joi.object().keys({
    type: Joi.string().valid(Workflow.Managed),
    buildType: Joi.string()
      .valid(...Object.values(ManagedBuildType))
      .default(ManagedBuildType.AppBundle),
    username: Joi.string(),
  }),
);

export { ManagedAndroidJob, ManagedAndroidJobSchema, ManagedBuildType };
